import React,{FC} from 'react';
import styled from 'styled-components';

import {HeroCardProps} from "./HeroCard.types";
import Text from "../Text/Text";

const StyledHeroCard = styled.div<HeroCardProps>`
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("${props => props.src}");
    cursor: ${props => props.disabled ? "not-allowed" : "cursor"};
    opacity: ${props => props.disabled ? 0.5 : 1};
`;

const HeroCard: FC<HeroCardProps> = ({disabled, title, text, src, ...props}) => {
    return (
        <StyledHeroCard disabled={disabled} background-image={src} {...props}>
            <Text disabled={disabled} centered="true" display="block" size="25px" text={title} />
            <Text disabled={disabled} centered="true" display="block" size="15px" text={text} />
        </StyledHeroCard>
    )
};

export default HeroCard;